   -webkit-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, .3);
           box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, .3);
   -webkit-transition: -webkit-box-shadow .2s linear;
   transition: -webkit-box-shadow .2s linear;
   transition: box-shadow .2s linear;
   transition: box-shadow .2s linear, -webkit-box-shadow .2s linear;
   cursor: pointer;
}
.img-wrap:hover {
      -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, .3);
              box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, .3);
   }
.pop-up {
   width: 150px;
   height: 150px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   position: absolute;
   top: 100%;
   left: 50%;
   -webkit-transform: translateX(-50%);
           transform: translateX(-50%);
   background: #fff;
   z-index: 9;
   -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, .75);
           box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, .75);
